import { Pool } from "pg";
import { inject, singleton } from "tsyringe";

import { Configuration } from "@infrastructure/config/global";

import { IDbConnection } from "../db-connection.interface";

@singleton()
export class PostgresSQLProvider implements IDbConnection {
  private readonly client: Pool;

  constructor(
    @inject("Configuration")
    private readonly config: Configuration
  ) {
    this.client = new Pool({
      user: this.config.getDbConfig().username,
      password: this.config.getDbConfig().password,
      host: this.config.getDbConfig().host,
      database: this.config.getDbConfig().database,
      port: this.config.getDbConfig().port,
    });
  }

  async query(query: string, values?: any[]): Promise<any> {
    const client = await this.client.connect();
    try {
      return await client.query(query, values).then((result) => result || []);
    } catch (e) {
      console.log(e);
    } finally {
      client.release();
    }
  }
}
