import * as console from "console";
import { StringValidator } from "src/infrastructure/utils/validators/stringValidator";

import { InternalServerError } from "@presentation/errors/http-errors/internal-server-error";

import { CompanyEntity } from "./company-entity";

export class OfferAvailableEntity {
  constructor(
    private readonly hasOffer: boolean,
    private readonly companyData: CompanyEntity
  ) {
    this.isReady();
  }

  isReady(): void {
    if (
      !StringValidator.isValidString(this.companyData.name) ||
      !StringValidator.isValidString(this.companyData.cnpjDigits)
    ) {
      console.error(
        `missing offer available data, this mas the object built ${JSON.stringify(
          this.toJSON()
        )}`
      );
      throw new InternalServerError(
        93,
        "Erro ao chamadas apis parceiros",
        `missing offer available data, this mas the object built ${JSON.stringify(
          this.toJSON()
        )}`
      );
    }
  }

  toJSON(): object {
    return {
      hasOffer: this.hasOffer,
      companyData: this.companyData,
    };
  }
}
