import dotenv from "dotenv";
import { singleton } from "tsyringe";

import { DbConfigurationParams } from "./connection";

@singleton()
export class Configuration {
  private readonly dbConfig: DbConfigurationParams;
  private readonly jwtSignKey: string;
  private readonly jwtExpirationTime: string;
  private readonly encryptKey: string;
  private readonly awsAccessKey: string;
  private readonly awsClientSecret: string;
  private readonly LOGGER_USER: string;
  private readonly LOGGER_PASSWORD: string;
  private readonly LOGGER_HOST: string;

  private readonly DB_HOST: string;
  private readonly DB_NAME: string;
  private readonly DB_USER: string;
  private readonly DB_PASSWORD: string;
  private readonly DB_PORT: string;

  constructor() {
    dotenv.config({
      path: `${process.cwd()}/.env.${process.env.NODE_ENV || "development"}`,
    });
    this.dbConfig = new DbConfigurationParams();
    this.jwtSignKey = process.env.JWT_SIGN_KEY;
    this.jwtExpirationTime = process.env.JWT_EXPIRATION_TIME;
    this.encryptKey = process.env.ENCRYPT_KEY;
    this.awsAccessKey = process.env.AWS_ACCESS_KEY_ID;
    this.awsClientSecret = process.env.AWS_SECRET_ACCESS_KEY;
    this.LOGGER_USER = process.env.LOGGER_USER;
    this.LOGGER_PASSWORD = process.env.LOGGER_PASSWORD;
    this.LOGGER_HOST = process.env.LOGGER_HOST;

    this.DB_HOST = process.env.DB_HOST;
    this.DB_NAME = process.env.DB_NAME;
    this.DB_USER = process.env.DB_USER;
    this.DB_PASSWORD = process.env.DB_PASSWORD;
    this.DB_PORT = process.env.DB_PORT;
  }

  getAwsAccessKey(): string {
    return this.awsAccessKey;
  }

  getAwsClientSecret(): string {
    return this.awsClientSecret;
  }

  getDbConfig(): DbConfigurationParams {
    return this.dbConfig;
  }

  getJwtSignKey(): string {
    return this.jwtSignKey;
  }

  getJwtExpirationTime(): string {
    return this.jwtExpirationTime;
  }

  getEncryptKey(): string {
    return this.encryptKey;
  }

  getLoggerUser(): string {
    return this.LOGGER_USER;
  }

  getLoggerPassword(): string {
    return this.LOGGER_PASSWORD;
  }

  getLoggerHost(): string {
    return this.LOGGER_HOST;
  }

  getDBHost(): string {
    return this.DB_HOST;
  }

  getDBName(): string {
    return this.DB_NAME;
  }

  getDBUser(): string {
    return this.DB_USER;
  }

  getDBPassword(): string {
    return this.DB_PASSWORD;
  }

  getDBPort(): string {
    return this.DB_PORT;
  }
}
