import { IConciliationCompanyDataRepository } from "src/infrastructure/repositories/conciliation-company-data.interface";
import { IConciliationRepository } from "src/infrastructure/repositories/conciliation.interface";
import { IMachineSellingPointRepository } from "src/infrastructure/repositories/machine-selling-point.interface";
import { inject, injectable } from "tsyringe";

import { ICompanyService } from "@application/interfaces/company/company-service-interface";
import IMachineSellingPoint from "@application/interfaces/machine/machine-selling-point.interface";
import { ConciliationCompanyEntity } from "@domain/entities/conciliation-company-entity";
import { ReconciliationActivationEntity } from "@domain/entities/reconciliation-activation-entity";
import { ReconciliationContactTiEntity } from "@domain/entities/reconciliation-contact-ti-entity";
import { ReconciliationEftProviderEntity } from "@domain/entities/reconciliation-eft-provider-entity";
import { ReconciliationIntegratingCompanyEntity } from "@domain/entities/reconciliation-integrating-company-entity";

@injectable()
export class MachineSellingPointService {
  constructor(
    @inject("CompanyService")
    private companyService: ICompanyService,
    @inject("MachineSellingPointRepository")
    private littleMachineRepository: IMachineSellingPointRepository,
    @inject("ConciliationCompanyRepository")
    private conciliationCompanyDataRepository: IConciliationCompanyDataRepository,
    @inject("ConciliationRepository")
    private conciliationRepository: IConciliationRepository
  ) {}

  public async execute(input: IMachineSellingPoint): Promise<void> {
    const { machineId, sellingPointId, cnpj, conciliation } = input;

    const machineInfo = await this.companyService.getMachineById(machineId);

    if (!(machineInfo.toJSON().name.toLocaleLowerCase() === "pin pad")) {
      await this.littleMachineRepository.insert(machineId, sellingPointId);
    } else {
      let companyId;
      const reconciliationContactTiEntity = new ReconciliationContactTiEntity({
        ...conciliation.itContact,
      });
      const reconciliationActivationEntity = new ReconciliationActivationEntity(
        {
          ...conciliation.activation,
        }
      );
      const reconciliationEftProviderEntity =
        new ReconciliationEftProviderEntity({
          ...conciliation.eftProvider,
        });

      const conciliationCompany =
        await this.conciliationCompanyDataRepository.getConciliationCompanyByCnpj(
          cnpj
        );

      const reconciliationIntegratingCompanyEntity =
        new ReconciliationIntegratingCompanyEntity({
          ...conciliation.integratingCompany,
        });

      if (!conciliationCompany.toJSON().homologated) {
        const conciliationCompanyEntity = new ConciliationCompanyEntity({
          ...conciliation.companyData,
        });

        companyId = await this.conciliationCompanyDataRepository.insert(
          conciliationCompanyEntity.toJSON()
        );
      }

      this.conciliationRepository.insertItContact({
        conciliationCompanyDataId: companyId ?? conciliationCompany.toJSON().id,
        ...reconciliationContactTiEntity.toJSON(),
      });

      this.conciliationRepository.insertItEftProvider({
        conciliationCompanyDataId: companyId ?? conciliationCompany.toJSON().id,
        ...reconciliationEftProviderEntity.toJSON(),
      });

      this.conciliationRepository.insertIntegratingCompany({
        conciliationCompanyDataId: companyId ?? conciliationCompany.toJSON().id,
        ...reconciliationIntegratingCompanyEntity.toJSON(),
      });

      this.conciliationRepository.insertActivation({
        conciliationCompanyDataId: companyId ?? conciliationCompany.toJSON().id,
        ...reconciliationActivationEntity.toJSON(),
      });

      await this.littleMachineRepository.insert(machineId, sellingPointId);
    }
  }
}
