import { inject, injectable } from "tsyringe";

import { IDbConnection } from "@adapter/database/providers/db-connection.interface";

import { ICompanyRepository } from "./company.interafce";

@injectable()
export default class CompanyRepository implements ICompanyRepository {
  constructor(
    @inject("DbProvider")
    private readonly connection: IDbConnection
  ) {}

  async getCompanyIdByCnpj(cnpj: string): Promise<string> {
    const { rows } = await this.connection.query(
      `SELECT * FROM "company" WHERE "cnpj"= $1`,
      [cnpj]
    );
    return rows[0]?.id;
  }
}
