import axios from "axios";
import * as process from "process";
import { injectable } from "tsyringe";

import { ICompanyService } from "@application/interfaces/company/company-service-interface";
import { IRegisterPointSalesCompanyServiceResponse } from "@application/interfaces/company/registe-point-sales-company-service-response.interface";
import { IRegisterPointSalesCompanyServiceRequest } from "@application/interfaces/company/register-point-sales-company-service-request.terface";
import { IProposalAccountBank } from "@application/interfaces/proposal/proposal-account-bank.interface";
import { MachineEntity } from "@domain/entities/machine-entity";
import { BadRequestError } from "@presentation/errors/http-errors/bad-request-error";
import { InternalServerError } from "@presentation/errors/http-errors/internal-server-error";

@injectable()
export class CompanyService implements ICompanyService {
  private store: IRegisterPointSalesCompanyServiceRequest[] = [];
  private readonly baseURL: string;
  private readonly apiKey: string;

  constructor() {
    this.baseURL = process.env.COMPANY_BASE_URL;
    this.apiKey = process.env.COMPANY_API_KEY;
  }

  async proposalAnticipationRate(
    id: string,
    idAnticipationRate: string
  ): Promise<boolean> {
    const response = await axios.patch(
      `${this.baseURL}/proposal/anticipation-rate`,
      JSON.stringify({ id, idAnticipationRate }),
      {
        headers: {
          APIKEY: this.apiKey,
          "Content-Type": "application/json",
        },
      }
    );
    if (response.data?.code === 0) return true;
    if (response.data?.code !== 0) {
      throw new BadRequestError(94, "company service", response.data.message);
    }
  }

  async proposalAccountBank(data: IProposalAccountBank): Promise<boolean> {
    const response = await axios.patch(
      `${this.baseURL}/proposal/account-bank`,
      JSON.stringify({ ...data }),
      {
        headers: {
          APIKEY: this.apiKey,
          "Content-Type": "application/json",
        },
      }
    );
    if (response.data?.code === 0) return true;
    if (response.data?.code !== 0) {
      throw new BadRequestError(94, "company service", response.data.message);
    }
  }

  registerAddress(
    request: IRegisterPointSalesCompanyServiceRequest
  ): Promise<IRegisterPointSalesCompanyServiceResponse> {
    try {
      // const response = await axios.post(
      //   `${this.baseURL}/analysis/pj/new-address`,
      //   JSON.stringify({ ...request }),
      //   {
      //     headers: {
      //       APIKEY: this.apiKey,
      //       "Content-Type": "application/json",
      //     },
      //   }
      // );
      // if (response.data?.code === 0) return response.data;
      // if (response.data?.code === 1 || response.data?.code === 2) {
      //   throw new BadRequestError(94, "company service", response.data.message);
      // }

      this.store.push(request);

      return new Promise((resolve) => {
        setTimeout(() => {
          resolve({
            cnpj: request.cnpj,
            cep: request.cep,
            streetName: request.street,
            complement: request.complement,
            referencePoint: request?.referencePoint,
            neighborhood: request.neighborhood,
            city: request.city,
            state: request.state,
            number: request.number,
          });
        }, 2000);
      });
    } catch (error) {
      throw new BadRequestError(
        94,
        "company service",
        "Erro ao conectar os serviços co company service"
      );
    }
  }

  async getMachineById(id: string): Promise<MachineEntity> {
    try {
      const response = await axios.get(`${this.baseURL}/machine/${id}/info`, {
        headers: {
          APIKEY: this.apiKey,
          "Content-Type": "application/json",
        },
      });
      return response.data as MachineEntity;
    } catch (error) {
      throw new InternalServerError(
        98,
        "Erro no servico do compnay service",
        error
      );
    }
  }
}
