import { StringValidator } from "@infrastructure/utils/validators/stringValidator";
import { BadRequestError } from "@presentation/errors/http-errors/bad-request-error";

export class ReconciliationContactTiEntity {
  constructor(
    protected readonly props: { name: string; email: string; phone: string }
  ) {
    this.isReady();
  }

  isReady(): void {
    if (
      !StringValidator.isValidString(this.props.name) ||
      !StringValidator.isValidString(this.props.email) ||
      !StringValidator.isValidString(this.props.phone)
    ) {
      throw new BadRequestError(
        94,
        "Body não identificado",
        `Body inválido, esse foi o dado enviado pelo cliente: ${JSON.stringify({
          name: this.props.name,
          email: this.props.email,
          phone: this.props.phone,
        })}`
      );
    }
  }

  toJSON() {
    return {
      name: this.props.name,
      email: this.props.email,
      phone: this.props.phone,
    };
  }
}
