import { StringValidator } from "src/infrastructure/utils/validators/stringValidator";

import { BadRequestError } from "@presentation/errors/http-errors/bad-request-error";

export class ConciliationCompanyEntity {
  constructor(
    protected readonly props: {
      name: string;
      cnpj: string;
      email: string;
      responsibleEstablishment: string;
      usedLayout: string;
      homologated: boolean;
      id?: string;
    }
  ) {
    this.isReady();
  }

  isReady(): void {
    if (
      !StringValidator.isValidString(this.props.name) ||
      !StringValidator.isValidString(this.props.cnpj) ||
      !StringValidator.isValidString(this.props.email) ||
      !StringValidator.isValidString(this.props.responsibleEstablishment) ||
      !StringValidator.isValidString(this.props.usedLayout)
    ) {
      throw new BadRequestError(
        94,
        "Body não identificado",
        `Body inválido, esse foi o dado enviado pelo cliente: ${JSON.stringify({
          name: this.props.name,
          cnpj: this.props.cnpj,
          email: this.props.email,
          responsibleEstablishment: this.props.responsibleEstablishment,
          usedLayout: this.props.usedLayout,
          homologated: this.props.homologated,
        })}`
      );
    }
  }

  toJSON() {
    return {
      id: this.props.id,
      name: this.props.name,
      cnpj: this.props.cnpj,
      email: this.props.email,
      responsibleEstablishment: this.props.responsibleEstablishment,
      usedLayout: this.props.usedLayout,
      homologated: this.props.homologated,
    };
  }
}
