import { inject, injectable } from "tsyringe";

import { BadRequestResponse } from "@adapter/responses";
import { IOfferAvailable } from "@application/interfaces/offer/offer-available-interface";
import { IOfferDetails } from "@application/interfaces/offer/offer-details.interface";
import { IOfferAvailableRequest } from "@application/interfaces/offer/offerAvailable.request.interface";
import { CompanyEntity } from "@domain/entities/company-entity";
import { OfferAvailableEntity } from "@domain/entities/offer-available-entity";
import {
  HttpResponse,
  IHttpClientService,
} from "@infrastructure/adapter/http-client/http-client-interface";
import { InternalServerError } from "@presentation/errors/http-errors/internal-server-error";
import { NotFoundError } from "@presentation/errors/http-errors/not-found-error";

@injectable()
export class OfferAvailableService implements IOfferAvailable {
  private readonly baseURL: string;
  private readonly apiKey: string;

  constructor(
    @inject("HttpClientService")
    private httpClientService: IHttpClientService
  ) {
    this.baseURL = process.env.COMPANY_BASE_URL;
    this.apiKey = process.env.COMPANY_API_KEY;
  }

  public async execute(
    offerAvailableRequest: IOfferAvailableRequest
  ): Promise<OfferAvailableEntity> {
    const cnpj = offerAvailableRequest.cnpj;

    const retOffer = await this.httpClientService.get(
      `${this.baseURL}/offer/inquiry/available/${cnpj}`,
      {
        headers: {
          APIKEY: this.apiKey,
          "Content-Type": "application/json",
        },
      },
      1
    );

    if (retOffer.status !== 200) {
      throw new NotFoundError(
        94,
        "dados de ofertas não encontrado",
        `não encontrada oferta  para o cnpj ${cnpj}`
      );
    }

    const retCompany = (await this.httpClientService
      .get(
        `${this.baseURL}/get-company-pre-registration/${cnpj}`,
        {
          headers: {
            APIKEY: this.apiKey,
            "Content-Type": "application/json",
          },
        },
        1
      )
      .catch((err) => {
        if (err.response.status === 404) {
          throw new NotFoundError(
            94,
            "dados da empresa nao encontrado",
            `não encontrado dados da empresa para o cnpj ${cnpj}`
          );
        }
      })) as HttpResponse;

    const companyData = retCompany.data.data as CompanyEntity;

    const offerEntity = new OfferAvailableEntity(true, companyData);

    return offerEntity;
  }

  async getOfferById(id: string): Promise<IOfferDetails> {
    const [machineRequest, anticipationRequest, ratesRequest, planRequest] =
      await Promise.all([
        this.getMachines(id),
        this.getAnticipations(id),
        this.getRates(id),
        this.getOffer(id),
      ]);

    if (
      [
        planRequest.status,
        anticipationRequest.status,
        ratesRequest.status,
        machineRequest.status,
      ].includes(500)
    ) {
      console.log("refafasd");
      throw new InternalServerError();
    }

    if (planRequest.status !== 200) {
      throw new BadRequestResponse(
        planRequest.data,
        "Id inválido ou não encontrado"
      );
    }

    const response: IOfferDetails = {
      offer: planRequest.data,
      anticipations: (anticipationRequest.data ?? []).filter(
        (val) => val.accountAfinz === false
      ),
      anticipationsAfinz: (anticipationRequest.data ?? []).filter(
        (val) => val.accountAfinz === true
      ),
      machine: machineRequest.data ?? [],
      rates: ratesRequest.data ?? [],
    };

    return response as IOfferDetails;
  }

  async getMachines(id: string): Promise<HttpResponse> {
    console.log(2);
    return this.httpClientService.get(
      `${this.baseURL}/offer-demo/card-machine/by-plan/${id}`,
      {
        headers: {
          APIKEY: this.apiKey,
          "Content-Type": "application/json",
        },
      },
      1
    );
  }

  async getAnticipations(id: string): Promise<HttpResponse> {
    console.log(3);
    return this.httpClientService.get(
      `${this.baseURL}/anticipation/by-plan/${id}`,
      {
        headers: {
          APIKEY: this.apiKey,
          "Content-Type": "application/json",
        },
      },
      1
    );
  }

  async getRates(id: string): Promise<HttpResponse> {
    console.log(4);
    return this.httpClientService.get(
      `${this.baseURL}/offer-demo/rates/by-plan/${id}`,
      {
        headers: {
          APIKEY: this.apiKey,
          "Content-Type": "application/json",
        },
      },
      1
    );
  }

  async getOffer(id: string): Promise<HttpResponse> {
    console.log(5);
    return this.httpClientService.get(
      `${this.baseURL}/offer-demo/plan/${id}`,
      {
        headers: {
          APIKEY: this.apiKey,
          "Content-Type": "application/json",
        },
      },
      1
    );
  }
}
