import { injectable } from "tsyringe";

import { HttpClientService } from "@adapter/http-client/http-client.service";
import { ISettlementBank } from "@application/interfaces/general/settlement-bank.interface";
import { InternalServerError } from "@presentation/errors/http-errors/internal-server-error";

@injectable()
export class GeneralService {
  private readonly baseURL: string;
  private readonly apiKey: string;
  private readonly http;
  constructor() {
    this.baseURL = process.env.COMPANY_BASE_URL;
    this.apiKey = process.env.COMPANY_API_KEY;
    this.http = new HttpClientService();
  }

  async settlementBankList(): Promise<ISettlementBank[]> {
    const response = await this.http.get(`${this.baseURL}/settlement-bank`, {
      headers: {
        APIKEY: this.apiKey,
        "Content-Type": "application/json",
      },
    });

    if (response.status === 500) {
      throw new InternalServerError(
        94,
        "general service",
        response.data.message
      );
    }

    const list: ISettlementBank[] = response.data.data;
    return list;
  }
}
