import { inject, injectable } from "tsyringe";

import { IDbConnection } from "@adapter/database/providers/db-connection.interface";
import { ConciliationCompanyEntity } from "@domain/entities/conciliation-company-entity";
import { InternalServerError } from "@presentation/errors/http-errors/internal-server-error";

import {
  IConciliationCompany,
  IConciliationCompanyDataRepository,
} from "./conciliation-company-data.interface";

@injectable()
export default class ConciliationCompanyRepository
  implements IConciliationCompanyDataRepository
{
  constructor(
    @inject("DbProvider")
    private readonly connection: IDbConnection
  ) {}
  async getConciliationCompanyByCnpj(
    cnpj: string
  ): Promise<ConciliationCompanyEntity> {
    const { rows } = await this.connection.query(
      `SELECT * FROM "conciliation_company_data" WHERE "cnpj" = $1 LIMIT 1`,
      [cnpj]
    );

    return rows[0] as ConciliationCompanyEntity;
  }

  async insert(entity: IConciliationCompany): Promise<string> {
    const {
      cnpj,
      email,
      homologated,
      name,
      responsibleEstablishment,
      usedLayout,
    } = entity;
    try {
      const { rows } = await this.connection.query(
        `INSERT INTO "conciliation_company_data" ("id","name","cnpj","email","responsibleEstablishment","useLayout","homologated","isActive", "createdAt", "updatedAt") VALUES (DEFAULT,$1,$2,$3,$4,$5,$6,DEFAULT, DEFAULT, DEFAULT) RETURNING *`,
        [name, cnpj, email, responsibleEstablishment, usedLayout, homologated]
      );

      return rows[0].id;
    } catch (error) {
      throw new InternalServerError(
        98,
        "erro ao cadastrar os dados na tabela [little_machine_selling_point]"
      );
    }
  }
}
