import axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse } from "axios";
import { injectable } from "tsyringe";

import { IHttpClientService } from "@infrastructure/adapter/http-client/http-client-interface";

export type HttpResponse = Omit<
  AxiosResponse,
  "headers" | "config" | "request"
>;

@injectable()
export class HttpClientService implements IHttpClientService {
  protected readonly httpClient: AxiosInstance;

  constructor() {
    this.httpClient = axios.create({
      timeout: 30000,
      headers: { "Content-Type": "application/json" },
    });
  }

  async get<T = any>(
    url: string,
    config?: AxiosRequestConfig,
    attempt = 0
  ): Promise<HttpResponse> {
    try {
      return await this.httpClient.get<T>(url, config);
    } catch (error) {
      console.log(error);
      if (attempt >= 3) {
        throw error;
      }

      return this.get<T>(url, config, attempt + 1);
    }
  }

  async post<T = any>(
    url: string,
    data: any,
    config?: AxiosRequestConfig,
    attempt = 0
  ): Promise<HttpResponse> {
    try {
      return await this.httpClient.post<T>(url, data, config);
    } catch (error) {
      if (attempt >= 3) {
        throw error;
      }

      return this.post<T>(url, data, config, attempt + 1);
    }
  }

  async put<T = any>(
    url: string,
    data: any,
    config?: AxiosRequestConfig,
    attempt = 0
  ): Promise<HttpResponse> {
    try {
      return await this.httpClient.put<T>(url, data, config);
    } catch (error) {
      if (attempt >= 3) {
        throw error;
      }
      return this.put<T>(url, data, config, attempt + 1);
    }
  }

  async patch<T = any>(
    url: string,
    data: any,
    config?: AxiosRequestConfig,
    attempt = 0
  ): Promise<HttpResponse> {
    try {
      return await this.httpClient.patch<T>(url, data, config);
    } catch (error) {
      if (attempt >= 3) {
        throw error;
      }

      return this.patch<T>(url, data, config, attempt + 1);
    }
  }

  async delete<T = any>(
    url: string,
    config?: AxiosRequestConfig,
    attempt = 0
  ): Promise<HttpResponse> {
    try {
      return await this.httpClient.delete<T>(url, config);
    } catch (error) {
      if (attempt >= 3) {
        throw error;
      }

      return this.delete<T>(url, config, attempt + 1);
    }
  }
}
