import { StringValidator } from "src/infrastructure/utils/validators/stringValidator";

import { IAddressType } from "@domain/enums/point-of-sales/point-of-sales";
import { BadRequestError } from "@presentation/errors/http-errors/bad-request-error";

export class PointSalesEntity {
  constructor(
    protected readonly _name: string,
    protected readonly _cnpj: string,
    protected readonly _branchCnpj: string,
    protected readonly _address: IAddressType
  ) {
    this.isReady();
  }

  get name(): string {
    return this._name;
  }

  get cnpj(): string {
    return this._cnpj;
  }
  get branchCnpj(): string {
    return this._branchCnpj;
  }
  get address(): IAddressType {
    return this._address;
  }
  isReady() {
    if (
      !StringValidator.isValidString(this.name) ||
      !StringValidator.isValidString(this.cnpj) ||
      !StringValidator.isValidString(this.branchCnpj)
    ) {
      throw new BadRequestError(
        94,
        "Body não identificado",
        `Body inválido, esse foi o dado enviado pelo cliente: ${JSON.stringify({
          name: this.name,
          cnpj: this.cnpj,
          branchCnpj: this.cnpj,
          address: this.address,
        })}`
      );
    }
  }

  toJSON(): object {
    return {
      name: this.name,
      cnpj: this.cnpj,
      branchCnpj: this.cnpj,
      address: this.address,
    };
  }
}
