import { APIGatewayProxyEventStageVariables } from "aws-lambda";
import { SSM } from "aws-sdk";
import * as dotenv from "dotenv";
import { getParam } from "src/infrastructure/utils/getParamenterValue";
import { injectable } from "tsyringe";

@injectable()
export class EnvironmentVariables {
  private readonly client = new SSM({
    region: "sa-east-1",
    credentials: {
      sessionToken: process.env.AWS_SESSION_TOKEN,
      accessKeyId: process.env.AWS_ACCESS_KEY_ID,
      secretAccessKey: process.env.AWS_SECRET_ACCESS_KEY,
    },
  });

  public readonly ready: Promise<void>;

  constructor() {
    dotenv.config();
  }

  public setStageToEnv(stageVariables: APIGatewayProxyEventStageVariables) {
    if (!stageVariables || Object.keys(stageVariables).length === 0) return;
    Object.entries(stageVariables).forEach((env: any) => {
      process.env[env[0]] = env[1];
    });
  }

  private async fillEnvs(): Promise<void> {
    const envs = {
      UNICO_BASE_URL: "AFZ_UNICO_BASE_URL",
      UNICO_AUTH_URL: "AFZ_UNICO_AUTH_URL",
      UNICO_API_KEY: "AFZ_UNICO_API_KEY",
      UNICO_ISS: "AFZ_UNICO_ISS",
      UNICO_SECRET_KEY: "AFZ_UNICO_SECRET_KEY",

      DB_HOST: "AFZ_APP_DB_HOST",
      DB_NAME: "AFZ_APP_DB_NAME",
      DB_USER: "AFZ_APP_DB_USER",
      DB_PASSWORD: "AFZ_APP_DB_PASSWORD",
      DB_PORT: "AFZ_APP_DB_PORT",
      COMPANY_BASE_URL: "AFZ_COMPANY_SERVICE_URL_API",
    };

    function getObjKey(obj, value) {
      return Object.keys(obj).find((key) => obj[key] === value);
    }

    await Promise.all(
      Object.values(envs).map(async (env) => {
        const env_key = getObjKey(envs, env);

        // buscando parâmetro na aws
        const param = await getParam(String(env), this.client);

        // atribuindo na variável local
        process.env[env_key] = String(param?.Value);
      })
    );
  }
}
