/* eslint-disable no-nested-ternary */
import "reflect-metadata";

import { HttpClientService } from "src/infrastructure/adapter/http-client/http-client.service";
import { ICompanyRepository } from "src/infrastructure/repositories/company.interafce";
import CompanyRepository from "src/infrastructure/repositories/company.repository";
import { IConciliationCompanyDataRepository } from "src/infrastructure/repositories/conciliation-company-data.interface";
import ConciliationCompanyRepository from "src/infrastructure/repositories/conciliation-company-data.repository";
import { IConciliationRepository } from "src/infrastructure/repositories/conciliation.interface";
import ConciliationRepository from "src/infrastructure/repositories/conciliation.repository";
import { IMachineSellingPointRepository } from "src/infrastructure/repositories/machine-selling-point.interface";
import MachineSellingPointRepository from "src/infrastructure/repositories/machine-selling-point.repository";
import { IPointSalesRepository } from "src/infrastructure/repositories/point-of-sales.interfce";
import PointSalesRepository from "src/infrastructure/repositories/point-of-sales.repository";
import UserNameRepository from "src/infrastructure/repositories/user-name.repository";
import { container } from "tsyringe";

import { CompanyService } from "@application/services/company/company-service";
import { PointSalesAppService } from "@application/services/company/point-sales-app.service";
import { GeneralService } from "@application/services/general/general-service";
import { MachineSellingPointService } from "@application/services/machine/machine-selling-point-app.service";
import { OfferAvailableService } from "@application/services/offer/offer-available.service";
import FeesBankDataService from "@application/services/proposal/fees-bank-data.service";
import { DbConnection } from "@infrastructure/adapter/database/db-config";
import { IDbConnection } from "@infrastructure/adapter/database/providers/db-connection.interface";
import { PostgresSQLProvider } from "@infrastructure/adapter/database/providers/postgresql/postgres.provider";

import { ICompanyService } from "../../application/interfaces/company/company-service-interface";
import { IPointSales } from "../../application/interfaces/company/point-sales-interface";
import { IOfferAvailable } from "../../application/interfaces/offer/offer-available-interface";
import { IHttpClientService } from "../adapter/http-client/http-client-interface";
import { EnvironmentVariables } from "./environment-variables";
import { Configuration } from "./global";

// configs
container.registerSingleton<Configuration>("Configuration", Configuration);
container.registerSingleton<DbConnection>(DbConnection.name, DbConnection);
container.registerSingleton<EnvironmentVariables>(
  "EnvironmentVariables",
  EnvironmentVariables
);

container.registerSingleton<IOfferAvailable>(
  OfferAvailableService.name,
  OfferAvailableService
);

container.registerSingleton<IHttpClientService>(
  HttpClientService.name,
  HttpClientService
);
container.registerSingleton<IPointSales>(
  PointSalesAppService.name,
  PointSalesAppService
);

container.registerSingleton<UserNameRepository>(
  UserNameRepository.name,
  UserNameRepository
);

container.registerSingleton<ICompanyService>(
  CompanyService.name,
  CompanyService
);

container.registerSingleton<FeesBankDataService>(
  FeesBankDataService.name,
  FeesBankDataService
);

container.registerSingleton<MachineSellingPointService>(
  MachineSellingPointService.name,
  MachineSellingPointService
);

container.registerSingleton<IMachineSellingPointRepository>(
  MachineSellingPointRepository.name,
  MachineSellingPointRepository
);

container.registerSingleton<IConciliationCompanyDataRepository>(
  ConciliationCompanyRepository.name,
  ConciliationCompanyRepository
);
container.registerSingleton<IConciliationRepository>(
  ConciliationRepository.name,
  ConciliationRepository
);
// providers
container.registerSingleton<IDbConnection>("DbProvider", PostgresSQLProvider);

// repositories
container.registerSingleton<IPointSalesRepository>(
  PointSalesRepository.name,
  PointSalesRepository
);
container.registerSingleton<ICompanyRepository>(
  CompanyRepository.name,
  CompanyRepository
);

// General
container.registerSingleton<GeneralService>(
  GeneralService.name,
  GeneralService
);

export { container };
