import { inject, injectable } from "tsyringe";

import { ICompanyService } from "@application/interfaces/company/company-service-interface";
import { IFeesBankDataService } from "@application/interfaces/proposal/fees-banks-data.service";
import { IFeesBankDataRequest } from "@application/interfaces/proposal/feesBank-data-request.interface";
import { InternalServerError } from "@presentation/errors/http-errors/internal-server-error";

@injectable()
export default class FeesBankDataService implements IFeesBankDataService {
  constructor(
    @inject("CompanyService")
    private companyService: ICompanyService
  ) {}
  async execute(request: IFeesBankDataRequest): Promise<void> {
    if (request.isAfinz) {
      try {
        await this.companyService.proposalAnticipationRate(
          request.id,
          request.idAnticipationRate
        );
      } catch (error) {
        throw new InternalServerError(
          98,
          "Falha ao tentar mudar a formação da taxa de antecipação da proposta",
          error
        );
      }
    } else {
      try {
        await Promise.all([
          this.companyService.proposalAnticipationRate(
            request.id,
            request.idAnticipationRate
          ),
          this.companyService.proposalAccountBank({
            ...request.bank,
            id: request.id,
          }),
        ]);
      } catch (error) {
        throw new InternalServerError(
          98,
          "Falha ao tentar inserir a proposta mudando apenas as informações da conta bancária relacionada a proposta",
          error
        );
      }
    }
  }
}
