import { OfferAvailableValidator } from "src/presentation/validators/offer/offer-available.validator";

import { IOfferDetails } from "@application/interfaces/offer/offer-details.interface";
import { OfferAvailableService } from "@application/services/offer/offer-available.service";
import { Inject } from "@infrastructure/config/inject";
import { OfferGetByIdValidator } from "@presentation/validators/offer/offer-by-id.validator";

export default class OfferController {
  private offerAvailableService;

  constructor() {
    this.offerAvailableService = Inject.getClass(OfferAvailableService);
  }

  async offerAvailable(cnpj: string) {
    const data = new OfferAvailableValidator(cnpj).isValid();
    return this.offerAvailableService.execute(data);
  }

  async getOfferById(id: string): Promise<IOfferDetails> {
    const data = new OfferGetByIdValidator(id).isValid();
    return this.offerAvailableService.getOfferById(data);
  }
}
