import "reflect-metadata";
import { APIGatewayProxyEvent } from "aws-lambda";

import { SuccessResponse } from "@adapter/responses";
import { container } from "@infrastructure/config";
import { LambdaHandler } from "@infrastructure/middleware/lambda/lambda-handler";
import OfferController from "@presentation/controllers/offer/OfferController";
import { middyfy } from "@resources/lambda";

const lambdaHandler = container.resolve(LambdaHandler);

const getOfferAvailableHandler = async (event: APIGatewayProxyEvent) =>
  lambdaHandler.perform(event, "GET", async () => {
    const controller = new OfferController();
    return new SuccessResponse(
      await controller.offerAvailable(event.pathParameters.cnpj)
    );
  });

export const main = middyfy(getOfferAvailableHandler);
