import { ICompanyRepository } from "src/infrastructure/repositories/company.interafce";
import { IPointSalesRepository } from "src/infrastructure/repositories/point-of-sales.interfce";
import { inject, injectable } from "tsyringe";

import { ICompanyService } from "@application/interfaces/company/company-service-interface";
import { IPointSales } from "@application/interfaces/company/point-sales-interface";
import IPointSalesRequest from "@application/interfaces/company/point-sales-request.interface";
import { PointSalesEntity } from "@domain/entities/point-sales-entity";
import { NotFoundError } from "@presentation/errors/http-errors/not-found-error";

@injectable()
export class PointSalesAppService implements IPointSales {
  constructor(
    @inject("CompanyService")
    private companyService: ICompanyService,
    @inject("PointSalesRepository")
    private pointSalesRepository: IPointSalesRepository,
    @inject("CompanyRepository")
    private companyRepository: ICompanyRepository
  ) {}

  public async execute(input: IPointSalesRequest): Promise<void> {
    // Dto neste caso é desnecessario, tendo em vista que a unica função é criar a entidade
    // que pode ser feito diretamente.
    const pointSalesEntity = new PointSalesEntity(
      input.name,
      input.cnpj,
      input.branchCnpj,
      input.address
    );

    const companyId = await this.companyRepository.getCompanyIdByCnpj(
      pointSalesEntity.cnpj
    );

    if (!companyId) {
      throw new NotFoundError(
        94,
        "Cnpj nao encontrado",
        "Esse cnpj nao foi encontrado ou seja nao existe em nossa bd"
      );
    }

    await Promise.all([
      this.companyService.registerAddress({
        cep: pointSalesEntity.address.postalCode,
        city: pointSalesEntity.address.city,
        cnpj: pointSalesEntity.branchCnpj,
        complement: pointSalesEntity.address.complement,
        neighborhood: pointSalesEntity.address.neighborhood,
        number: pointSalesEntity.address?.number,
        referencePoint: pointSalesEntity.address?.referencePoint,
        state: pointSalesEntity.address.state,
        street: pointSalesEntity.address.street,
      }),

      this.pointSalesRepository.insert({
        companyId,
        postalCode: pointSalesEntity.address.postalCode,
        city: pointSalesEntity.address.city,
        cnpj: pointSalesEntity.branchCnpj,
        complement: pointSalesEntity.address.complement,
        neighborhood: pointSalesEntity.address.neighborhood,
        number: pointSalesEntity.address?.number,
        referencePoint: pointSalesEntity.address?.referencePoint,
        state: pointSalesEntity.address.state,
        street: pointSalesEntity.address.street,
      }),
    ]);
  }
}
