import Joi from "joi";
import { JoiValidateHelper } from "src/infrastructure/utils/validators/joi-validate.helper";

import { BadRequestError } from "@presentation/errors/http-errors/bad-request-error";

export class OfferGetByIdValidator {
  readonly id: string;

  constructor(id: string) {
    this.validate(id);
    this.id = id;
  }

  validate(data: string) {
    const validators = {
      id: JoiValidateHelper.uuid().required(),
    };

    const errors = JoiValidateHelper.validate(Joi.object(validators), {
      id: data,
    });

    if (errors.errorMessage) {
      throw new BadRequestError(
        1,
        "Um ou mais campos inválidos",
        errors.errorMessage,
        errors.errorMessage
      );
    }
  }

  isValid() {
    return this.id;
  }
}
