// import { InternalError } from '@presentation/errors/internal-error';
import { SSM } from "aws-sdk";

interface IParameter {
  Name: string;
  Type: string;
  Value: string;
  Version: number;
  LastModifiedDate: string;
  ARN: string;
  DataType: string;
}

export async function getParam(
  parameter: string,
  client: SSM
): Promise<IParameter> {
  try {
    const data = await client
      .getParameter({ Name: parameter, WithDecryption: true })
      .promise();

    if ("Parameter" in data) {
      return data.Parameter as unknown as IParameter;
    }

    return Buffer.from(data.Parameter as any, "base64").toString(
      "ascii"
    ) as unknown as IParameter;
  } catch (error) {
    // throw new InternalError(error?.message);
    console.error(error);
  }
}
