import { StringValidator } from "@infrastructure/utils/validators/stringValidator";
import { BadRequestError } from "@presentation/errors/http-errors/bad-request-error";

export class ReconciliationEftProviderEntity {
  constructor(
    protected readonly props: { providerTef: string; connectionType: string }
  ) {
    this.isReady();
  }

  isReady(): void {
    if (
      !StringValidator.isValidString(this.props.providerTef) ||
      !StringValidator.isValidString(this.props.connectionType)
    ) {
      throw new BadRequestError(
        94,
        "Body não identificado",
        `Body inválido, esse foi o dado enviado pelo cliente: ${JSON.stringify({
          providerTef: this.props.providerTef,
          connectionType: this.props.connectionType,
        })}`
      );
    }
  }

  toJSON() {
    return {
      providerTef: this.props.providerTef,
      connectionType: this.props.connectionType,
    };
  }
}
