import Joi from "joi";
import { JoiValidateHelper } from "src/infrastructure/utils/validators/joi-validate.helper";

import { IOfferAvailableRequest } from "@application/interfaces/offer/offerAvailable.request.interface";
import { BadRequestError } from "@presentation/errors/http-errors/bad-request-error";

export class OfferAvailableValidator {
  cnpj: string;

  constructor(cnpj: string) {
    this.validate({ cnpj });
    this.cnpj = cnpj;
  }

  validate(data: IOfferAvailableRequest) {
    const validators = {
      cnpj: JoiValidateHelper.cnpj().required(),
    };

    const errors = JoiValidateHelper.validate(Joi.object(validators), data);

    if (errors.errorMessage) {
      // TODO Alterar para 422
      throw new BadRequestError(
        1,
        "Um ou mais campos inválidos",
        errors.errorMessage,
        errors.errorMessage
      );
    }
  }

  isValid(): IOfferAvailableRequest {
    return this;
  }
}
