import { inject, injectable } from "tsyringe";

import { IDbConnection } from "@adapter/database/providers/db-connection.interface";
import { InternalServerError } from "@presentation/errors/http-errors/internal-server-error";

import { IMachineSellingPointRepository } from "./machine-selling-point.interface";

@injectable()
export default class MachineSellingPointRepository
  implements IMachineSellingPointRepository
{
  constructor(
    @inject("DbProvider")
    private readonly connection: IDbConnection
  ) {}

  async insert(machineId: string, sellingPointId: string): Promise<boolean> {
    try {
      const { rows } = await this.connection.query(
        `INSERT INTO "machine_selling_point" ("id","machineId","sellingPointId","isActive", "createdAt", "updatedAt") VALUES (DEFAULT,$1, $2, DEFAULT, DEFAULT, DEFAULT) RETURNING *`,
        [machineId, sellingPointId]
      );

      return !!rows[0];
    } catch (error) {
      throw new InternalServerError(
        98,
        "erro ao cadastrar os dados na tabela [machine_selling_point]"
      );
    }
  }
}
