import { DayOfWeek, Period } from "@infrastructure/utils/enums/date.enum";
import { DateValidator } from "@infrastructure/utils/validators/dateValidator";
import { EnumValidator } from "@infrastructure/utils/validators/enumValidator";
import { StringValidator } from "@infrastructure/utils/validators/stringValidator";
import { BadRequestError } from "@presentation/errors/http-errors/bad-request-error";

export class ReconciliationActivationEntity {
  constructor(
    protected readonly props: {
      daysWeek: DayOfWeek;
      period: Period;
      endDate: Date;
      startDate: Date;
      observation: string;
    }
  ) {
    this.isReady();
  }

  isReady(): void {
    if (
      !StringValidator.isValidString(this.props.observation) ||
      !EnumValidator.isEnumValue<DayOfWeek>(this.props.daysWeek, DayOfWeek) ||
      !EnumValidator.isEnumValue<Period>(this.props.period, Period) ||
      !DateValidator.isValidDate(this.props.endDate) ||
      !DateValidator.isValidDate(this.props.startDate)
    ) {
      throw new BadRequestError(
        94,
        "Body não identificado",
        `Body inválido, esse foi o dado enviado pelo cliente: ${JSON.stringify({
          daysWeek: this.props.daysWeek,
          period: this.props.period,
          endDate: this.props.endDate,
          startDate: this.props.startDate,
          observation: this.props.observation,
        })}`
      );
    }
  }

  toJSON() {
    return {
      daysWeek: this.props.daysWeek,
      period: this.props.period,
      endDate: this.props.endDate,
      startDate: this.props.startDate,
      observation: this.props.observation,
    };
  }
}
