import { inject, injectable } from "tsyringe";

import { IDbConnection } from "@adapter/database/providers/db-connection.interface";

export interface IUserNameRepository {
  isInappropriateSocialName(socialName: string): Promise<boolean>;
}

@injectable()
export default class UserNameRepository implements IUserNameRepository {
  constructor(
    @inject("DbProvider")
    private readonly connection: IDbConnection
  ) {}

  async isInappropriateSocialName(socialName: string): Promise<boolean> {
    const { rows } = await this.connection.query(
      `SELECT * FROM "NomeSocialImproprio" WHERE LOWER("NomeImproprio") = LOWER($1) LIMIT 1`,
      [socialName]
    );
    return !!rows[0];
  }
}
